import Vue from 'vue'
import Router from 'vue-router'
import TwitterContent from '@/components/TwitterContent'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TwitterContent',
      component: TwitterContent
    },
    {
      path: '/search',
      name: 'TwitterContent',
      component: TwitterContent
    }
  ]
})



